import React from 'react'
import { Link } from 'react-router-dom';

import HeaderSolutions from '../../Components/HeaderSolutions/HeaderSolutions';
import Faq from '../../Components/FAQ/Faq';

import TroncalSIPHome from '../../Files/TroncalSIP/TroncalSIPHome.jpg'
import TroncalSIPCTA from '../../Files/TroncalSIP/TroncalSIPCTA.jpg'
import TroncalSip1 from '../../Files/TroncalSIP/TroncalSip1.svg'
import TroncalSip2 from '../../Files/TroncalSIP/TroncalSip2.svg'
import TroncalSip3 from '../../Files/TroncalSIP/TroncalSip3.svg'
import TroncalSIPDiagrama from '../../Files/TroncalSIP/TroncalSIPDiagrama.svg'

export default function TroncalSIP() {
    return (
        <div className="dark:bg-dark-800">

            {/*Header */}
            <HeaderSolutions 
            img={TroncalSIPHome}
            alt="Solución Troncal SIP"
            title="Actualice su Telefonía contratando el servicio de "
            subtitle="Troncal SIP"
            button="Contáctanos"
            />
            
            {/*Hero */}
            <div className="p-6 md:p-8">
              <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
                <div className="grid gap-10 lg:grid-cols-2">
                  <div className="flex flex-col lg:pt-7 md:pr-8 xl:pr-0 lg:max-w-lg">
                    <h3 className="pb-8 text-xl text-center md:text-left">Migre de la "vieja" tecnología PSTN con sus líneas analógicas que está por desaparecer, hacia lo moderno con troncales SIP sobre Internet para mayor <span className="text-maroon-600">flexibilidad</span>. </h3>
                    <div className="max-w-xl mb-6">
                      <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                        ¿Qué es una troncal SIP?
                      </h2>
                      <p className="text-base text-gray-700 md:text-lg">
                        Una troncal SIP (Session Initiation Protocol) es un servicio que permite a las empresas con una PBX utilizar
                        comunicaciones VoIP en tiempo real. Como resultado, un servicio de SIP trunking por lo general ofrece un mayor 
                        ahorro de costes y una mayor fiabilidad.
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center justify-center -mx-4 lg:pl-8">
                    <div className="flex flex-col items-end px-3">
                      <img
                        className="object-cover mb-6 rounded shadow-lg h-28 sm:h-48 xl:h-56 w-28 sm:w-48 xl:w-56"
                        src={TroncalSip1}
                        alt="Troncal SIP Lllamada 1"
                      />
                      <img
                        className="object-cover w-20 h-20 rounded shadow-lg sm:h-32 xl:h-40 sm:w-32 xl:w-40"
                        src={TroncalSip2}
                        alt="Troncal SIP Lllamada 2"
                      />
                    </div>
                    <div className="px-3">
                      <img
                        className="object-cover w-40 h-40 rounded shadow-lg sm:h-64 xl:h-80 sm:w-64 xl:w-80"
                        src={TroncalSip3}
                        alt="Troncal SIP Lllamada 3"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/*¿Cómo Funciona? */}

            <div className="px-4 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 ">
              <div className="mx-auto sm:text-center lg:max-w-3xl">
                <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
                  <div>
                    <p className="inline-block px-3 py-px mb-4 text-xs font-semibold tracking-wider text-white uppercase rounded-full bg-maroon-600">
                      Esquema
                    </p>
                  </div>
                  <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto">
                    <span className="relative inline-block">
                      <svg
                        viewBox="0 0 52 24"
                        fill="currentColor"
                        className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
                      >
                        <defs>
                          <pattern
                            id="5dc90b42-5ed4-45a6-8e63-2d78ca9d3d95"
                            x="0"
                            y="0"
                            width=".135"
                            height=".30"
                          >
                            <circle cx="1" cy="1" r=".7" />
                          </pattern>
                        </defs>
                        <rect
                          fill="url(#5dc90b42-5ed4-45a6-8e63-2d78ca9d3d95)"
                          width="52"
                          height="24"
                        />
                      </svg>
                      <span className="relative">¿</span>
                    </span>
                    Cómo funciona?
                  </h2>
                  <p className="text-base text-gray-700 md:text-lg">
                  Los troncales SIP son líneas telefónicas que funcionan sobre IP utilizando el protocolo SIP. 
                  Utilizando este protocolo estándar, los proveedores de servicios de telecomunicaciones (VoIP)
                  conectan uno o más canales a la PBX del cliente. 
                  Los números telefónicos y DIDs son enlazados al troncal SIP.
                  </p>
                </div>
                <div className="mx-1 shadow mb-9 lg:flex">
                  <img
                    className="w-full h-56 rounded shadow-lg md:object-cover sm:h-64 md:h-80 lg:h-96"
                    src={TroncalSIPDiagrama}
                    alt=""
                  />
                </div>
              </div>
            </div>

            

            {/*FAQ */}
            <div className="p-6 py-10 bg-gray-100 md:p-8">
                <Faq />
            </div>
            
            {/*Ventajas */}
            <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
              <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
                <div>
                  <p className="inline-block px-3 py-px mb-4 text-xs font-semibold tracking-wider text-teal-900 uppercase rounded-full bg-teal-accent-400">
                  Ventajas
                  </p>
                </div>
                <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto">
                  <span className="relative inline-block">
                    <svg
                      viewBox="0 0 52 24"
                      fill="currentColor"
                      className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
                    >
                      <defs>
                        <pattern
                          id="a0b40128-6963-4319-aeeb-471e92fa2d74"
                          x="0"
                          y="0"
                          width=".135"
                          height=".30"
                        >
                          <circle cx="1" cy="1" r=".7" />
                        </pattern>
                      </defs>
                      <rect
                        fill="url(#a0b40128-6963-4319-aeeb-471e92fa2d74)"
                        width="52"
                        height="24"
                      />
                    </svg>
                    <span className="relative">¿</span>
                  </span>{' '}
                  Por qué escoger a <span className="text-maroon-700">Schnelke</span>?
                </h2>
              </div>
              <div className="max-w-lg space-y-3 sm:mx-auto lg:max-w-xl">
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">
                    Las tarifas más bajas del mercado 
                  </span>
                </div>
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">    Alta Calidad de Audio </span>
                </div>
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">    Reducción de tu Recibo Telefonico </span>
                </div>
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">
                    Conectividad en cualquier parte del mundo 
                  </span>
                </div>
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">
                  Elasticidad 
                  </span>
                </div>
                <div className="flex items-center p-2 duration-300 transform border rounded shadow hover:scale-105 sm:hover:scale-110">
                  <div className="mr-2">
                    <svg
                      className="w-6 h-6 text-deep-purple-accent-400 sm:w-8 sm:h-8"
                      stroke="currentColor"
                      viewBox="0 0 52 52"
                    >
                      <polygon
                        strokeWidth="3"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        fill="none"
                        points="29 13 14 29 25 29 23 39 38 23 27 23"
                      />
                    </svg>
                  </div>
                  <span className="text-gray-800">
                  Seguridad 
                  </span>
                </div>
              </div>
            </div>
        

            <div id="pricing" className="w-full bg-gray-500" style={{backgroundImage: `url(${TroncalSIPCTA})`, backgroundPosition: 'center center', backgroundBlendMode: 'multiply', backgroundSize: 'cover'}}>
              <div className="container flex flex-col flex-wrap content-center justify-center p-4 py-20 mx-auto md:p-10">
                <h1 className="text-4xl antialiased font-semibold leading-none text-center text-gray-100 lg:text-5xl">¿Listo para mejorar la comunicación en tu empresa?</h1>
                <p className="pt-2 pb-8 text-lg antialiased text-center text-gray-300 md:text-xl">Personaliza tu solución de acuerdo a las necesidades de tu negocio.</p>
                <div className="flex items-center justify-center">
                  <Link to="/contacto" className="p-3 px-6 font-semibold text-white rounded-md bg-crismon-600">Contáctanos</Link>
                </div>
              </div>
            </div>
        </div>
    )
}



