import React from 'react'

import PbxImageHome from '../../Files/Pbx/PbxImageHome.jpg';
import PbxEnlazar from '../../Files/Pbx/PbxEnlazar.jpg';

import HeaderSolutions from '../../Components/HeaderSolutions/HeaderSolutions';
import Pricing1 from '../../Components/Pricing/Pricing1';
import PricingMobile1 from '../../Components/Pricing/PricingMobile1';
import Cta from '../../Components/CTA/Cta'

import { ChatAlt2Icon } from '@heroicons/react/outline'
import { PhoneIncomingIcon } from '@heroicons/react/outline'
import { InboxInIcon } from '@heroicons/react/outline'
import { UsersIcon } from '@heroicons/react/outline'


import { Link } from 'react-scroll';


export default function pbxCloud() {
    return (
        <div className="bg-gray-100 dark:bg-dark-800">
        
          {/*Header */}
          <HeaderSolutions 
          img={PbxImageHome}
          title="Comunícate, contécate y colabora profesionalmente "
          subtitle="con Schnelke"
          button="ver paquetes"
          />
          
          <div className="p-6 md:p-8">
            
            {/*Hero*/}
            <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
              <div className="grid gap-5 row-gap-8 lg:grid-cols-2">
                <div className="flex flex-col justify-center">
                  <div className="max-w-xl mb-6">
                    <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-center text-gray-900 md:text-left sm:text-4xl sm:leading-none">
                      Implementa un sistema de comunicación en la nube
                      <br className="hidden md:block" />
                      a un bajo costo y
                      <span className="relative px-1">
                        <span className="relative inline-block text-crismon-200">
                        desde cualquier dispositivo.
                        </span>
                      </span>
                    </h2>
                    <p className="text-base text-gray-700 md:text-lg">
                    Mantén enlazadas tus oficinas, sucursales o incluso tu domicilio con el solo acceso a internet. 
                    No importa la cantidad de extensiones que se requieran, 
                    obtén flexibilidad en la forma de usar tu red de telefonía y en la forma de pago.
                    </p>
                  </div>
                  
                  <div>
                    <div className="bg-white border-l-4 shadow-sm border-maroon-600">
                      <div className="h-full p-5 border border-l-0 rounded-r">
                        <h6 className="mb-2 font-semibold leading-5">
                        Paga solo por lo que usas 
                        </h6>
                        <p className="mb-5 text-sm text-gray-900">
                        o arma un paquete pre diseñado y adaptado a tus necesidades.
                        </p>
                        <Link smooth={true} duration={1000} to="pricing" className="hidden px-4 py-2 text-sm font-medium text-white transition-colors duration-200 rounded-md cursor-pointer md:inline-block bg-maroon-700 lg:w-auto hover:bg-crismon-100 focus:outline-none focus:bg-crismon-100">Ver paquetes</Link>
                      </div>
                    </div>
                  </div>
                </div>
                <div>
                  <img
                    className="object-cover w-full h-56 rounded shadow-lg sm:h-96"
                    src={PbxEnlazar}
                    alt="Schnelke Soluciones PBX"
                  />
                </div>
              </div>
            </div>

            {/*Servicios */}  
            <div className="bg-gray-100">
              <div className="relative py-3 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8"><h3 className="px-8 text-2xl font-bold">No pierdas servicios como:</h3></div>
              <div className="relative px-4 mx-auto py-7 sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20"> 
                <div className="absolute inset-x-0 top-0 items-center justify-center hidden overflow-hidden md:flex md:inset-y-0">
                  <svg
                    viewBox="0 0 88 88"
                    className="w-full max-w-screen-xl text-maroon-700"
                  >
                    <circle fill="currentColor" cx="44" cy="44" r="15.5" />
                    <circle
                      fillOpacity="0.2"
                      fill="currentColor"
                      cx="44"
                      cy="44"
                      r="44"
                    />
                    <circle
                      fillOpacity="0.2"
                      fill="currentColor"
                      cx="44"
                      cy="44"
                      r="37.5"
                    />
                    <circle
                      fillOpacity="0.3"
                      fill="currentColor"
                      cx="44"
                      cy="44"
                      r="29.5"
                    />
                    <circle
                      fillOpacity="0.3"
                      fill="currentColor"
                      cx="44"
                      cy="44"
                      r="22.5"
                    />
                  </svg>
                </div>
                <div className="relative grid gap-5 sm:grid-cols-2 lg:grid-cols-4">
                  <div className="flex flex-col justify-between overflow-hidden text-left transition-shadow duration-200 bg-white rounded shadow-xl group hover:shadow-2xl">
                    <div className="p-5">
                      <div className="flex items-center justify-center w-10 h-10 mb-4 rounded-full bg-red-50">
                        <ChatAlt2Icon className="w-6 h-6 text-oxford-500"/>
                      </div>
                      <p className="mb-2 font-bold">IVR (Interactive Voice Response)</p>
                      <p className="text-sm leading-5 text-gray-900">
                        Contestadora automática que distribuye eficientemente las llamadas de sus clientes hacia su negocio específicamente con quien desean hablar. 
                      </p>
                    </div>
                  </div>
                  <div className="flex flex-col justify-between overflow-hidden text-left transition-shadow duration-200 bg-white rounded shadow-xl group hover:shadow-2xl">
                    <div className="p-5">
                      <div className="flex items-center justify-center w-10 h-10 mb-4 rounded-full bg-red-50">
                        <PhoneIncomingIcon className="w-5 h-5 text-oxford-500"/>
                      </div>
                      <p className="mb-2 font-bold">Follow Me (Sigueme)</p>
                      <p className="text-sm leading-5 text-gray-900">
                        Sin importar donde se encuentre, nunca perderá las llamadas importantes ya que sus llamadas serán redirigidas a su ubicación. 
                      </p>
                    </div>
                  </div>
                  <div className="flex flex-col justify-between overflow-hidden text-left transition-shadow duration-200 bg-white rounded shadow-xl group hover:shadow-2xl">
                    <div className="p-5">
                      <div className="flex items-center justify-center w-10 h-10 mb-4 rounded-full bg-red-50">
                        <InboxInIcon className="w-5 h-5 text-oxford-500"/>
                      </div>
                      <p className="mb-2 font-bold">Buzón de Voz (Voicemail)</p>
                      <p className="text-sm leading-5 text-gray-900">
                        Te podrán dejar sus mensajes cuando este fuera de horario y lo podrá tener en su correo para escucharlo. 
                      </p>
                    </div>
                  </div>
                  <div className="flex flex-col justify-between overflow-hidden text-left transition-shadow duration-200 bg-white rounded shadow-xl group hover:shadow-2xl">
                    <div className="p-5">
                      <div className="flex items-center justify-center w-10 h-10 mb-4 rounded-full bg-red-50">
                        <UsersIcon className="w-5 h-5 text-oxford-500"/>
                      </div>
                      <p className="mb-2 font-bold">Conferencia (2 o más participantes)</p>
                      <p className="text-sm leading-5 text-gray-900">
                        Podrá realizar salas de conferencias para atender a sus clientes o realizar juntas con su colaboradores, para que sin importar la ubicación o circunstancias, su negocio continúe funcionando. 
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/*Precios */}
            <div id="pricing">
              <Pricing1/>
              <PricingMobile1 />
            </div>
          </div>

          {/*CTA */}
          <Cta />
          
      </div>
    );
}
